
import 'rxjs/add/operator/map';

import { HttpClient,HttpHeaders} from '@angular/common/http';

import { AppData } from './appdata';
import { CommonStorage } from './common-storage';
import { Injectable } from '@angular/core';
import { Observable } from 'rxjs';
import { UtilProvider } from './util-provider';

@Injectable()
export class AuthenticationService {
public token: string;
   BASE_URL = '';
    headers:any;
    options:any;
    platformName:string;
    deviceId = ''
    recentSearches: any = [];
constructor(
        private http: HttpClient,
        private appData:AppData,
        private util:UtilProvider,
        public commonStorage:CommonStorage
    ) {
// set token if saved in local storage
var currentUser = JSON.parse(localStorage.getItem('currentUser'));
this.token = currentUser && currentUser.token;
        this.platformName = this.util.platformName();
        this.headers = new Headers({ 'platform': this.platformName});
this.options = new HttpHeaders({ headers: this.headers });
        this.BASE_URL = this.appData.baseUrl();
        // console.log("AuthenticationService --> constructor, platformName ",this.token, this.platformName);
}

/**
 * login code 
 */
login(email: string, password: string): Observable<any> {
        this.deviceId = this.commonStorage.getItem('deviceId')?this.commonStorage.getItem('deviceId'):''
        //  return this.http.post(this.BASE_URL + 'api2/singup.json?', { "user" : {"email": email, "password": password , "password_confirmation": password,"first_name": "Bitla","mobile_number": "9658574120","dob": "2014-12-22"} }, this.options)
            return this.http.post(this.BASE_URL + 'api2/login.json?device_id='+this.deviceId, { "user" : {"email": email, "password": password} }, this.options)
.map((response: any) => {
// login successful if there's a jwt token in the response
                try{
                    let result = response
    if (result.success) {
                        // let token =  result.body.customer.authentication_token;
                        let token =  result.body.authentication_token;

    // set token property
    this.token = token;
                        // console.log("login --> ", token)
    
    // store email and auth token in local storage to keep user logged in between page refreshes
    localStorage.setItem('currentUser', JSON.stringify({ email: email, token: token, userFull: result.body.customer,user: this.setUser(result.body) }));
    
    // return true to indicate successful login
    return result;
    } else {
    // return false to indicate failed login
    return result;
    }
                }catch(e){
                    // console.log("Exception in AuthenticationService --> login()",e)
                    return {success:false,body:{message:"Login Failed try after sometime"}};
                }
});
}

otp(mobile_number:any): Observable<any> {
this.options = new HttpHeaders({ headers: this.headers });
        //  return this.http.post(this.BASE_URL + 'api2/singup.json?', { "user" : {"email": email, "password": password , "password_confirmation": password,"first_name": "Bitla","mobile_number": "9658574120","dob": "2014-12-22"} }, this.options)
            return this.http.post(this.BASE_URL + 'api2/validate_mobile_number.json?mobile_number='+mobile_number, this.options)
.map((response: any) => {
                try{
                    
                    let result = response.json()
                    // console.log("otp response --> authentication page", result)
    if (result.success) {
    // return true to indicate successful login
    return result;
    } else {
    // return false to indicate failed login
    return result;
    }
                }catch(e){
                    // console.log("Exception in AuthenticationService --> signup()",e)
                    return {success:false,body:{message:"otp verification Failed try after sometime"}};
                }
}); 
    }    


/**
 * signup code 
 */
    
    signup(signupData:any,referal_code:any): Observable<any> {
        this.deviceId = this.commonStorage.getItem('deviceId')?this.commonStorage.getItem('deviceId'):''
        this.headers = new Headers({ 'platform': 'Android'});
this.options = new HttpHeaders({ headers: this.headers });
        //  return this.http.post(this.BASE_URL + 'api2/singup.json?', { "user" : {"email": email, "password": password , "password_confirmation": password,"first_name": "Bitla","mobile_number": "9658574120","dob": "2014-12-22"} }, this.options)
        var url = this.BASE_URL + 'api2/signup.json?device_id='+this.deviceId;
        if(referal_code!=""){
            url = this.BASE_URL + 'api2/signup.json?device_id='+this.deviceId+'&referal_code='+referal_code
        }
            return this.http.post(url, signupData, this.options)
.map((response: any) => {
                try{
                    
    // login successful if there's a jwt token in the response
                    let result = response
                    // console.log("signup response --> authentication page", result)
    if (result.success) {
    // return true to indicate successful login
    return result;
    } else {
    // return false to indicate failed login
    return result;
    }
                }catch(e){
                    // console.log("Exception in AuthenticationService --> signup()",e)
                    return {success:false,body:{message:"Signup Failed try after sometime"}};
                }
}); 
    }    

logout(): void {
// clear token remove user from local storage to log user out
this.token = null;
localStorage.removeItem('currentUser');
}

    isLoggedIn():boolean{
var currentUser = JSON.parse(localStorage.getItem('currentUser'));
        if(currentUser && currentUser.token){
            return true;
        }
        return false;
    }

    setUser(currentUser){
// var currentUser = JSON.parse(localStorage.getItem('currentUser'));
// console.log(currentUser)
        return {
           name:currentUser.first_name,
           email:currentUser.email,
           dob:currentUser.date_of_birth,
           mobileNo:currentUser.mobile_number,
           age:currentUser.age,
           title:currentUser.title
        };
    }

    user():any{
        var currentUser = JSON.parse(localStorage.getItem('currentUser'));
        return currentUser?currentUser.user:{};
    }
    
    updateUserAuth(data:any){
        let token =  data.body.customer.authentication_token;
// set token property
this.token = token;
let email =  data.body.customer.email;
    //    console.log("login --> ", token)

// store email and auth token in local storage to keep user logged in between page refreshes
localStorage.setItem('currentUser', JSON.stringify({ email: email, token: token, userFull: data.body.customer,user: this.setUser(data.body.customer) }));
// return true to indicate successful login
return data;
    }

    forgotPassword(data){
        // console.log("data2",data);
        this.deviceId = this.commonStorage.getItem('deviceId')?this.commonStorage.getItem('deviceId'):''
        return this.http.post(this.BASE_URL + 'api2/forgot_password.json?device_id='+this.deviceId, { "user" : data }, this.options)
.map((response: any) => {
            try{
                let result = response
                // console.log("signup response --> authentication page", result)
if (result.success) {
// return true to indicate successful
return result;
} else {
// return false to indicate failed 
return result;
}
            }catch(e){
                // console.log("Exception in AuthenticationService --> forgotPassword()",e)
                return {success:false,body:{message:"Signup Failed try after sometime"}};
            }
 }); 
    }


    changePassword(data){
        return this.http.post(this.BASE_URL + 'api2/reset_password_with_mobile_number.json?key='+data.key+'&new_password='+data.newPassword+'&confirm_password='+data.confirmPassword+'&new_otp='+data.otp+'&mobile_number='+data.mobileNumber,'', this.options)
.map((response: any) => {
                        try{
                            let result: any = response
                            // console.log("signup response --> authentication page", result)
            if (result.success) {
            // return true to indicate successful
            return result;
            } else {
            // return false to indicate failed 
            return result;
            }
                        }catch(e){
                            // console.log("Exception in AuthenticationService --> forgotPassword()",e)
                            return {success:false,body:{message:"Signup Failed try after sometime"}};
                        }
 }); 
    }
   
    setRecentSearch(){
        // console.log("set recent search");
        this.recentSearches = this.commonStorage.localGet('recent-search-offline') ? this.commonStorage.localGet('recent-search-offline') : [];
    }

}